.App {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.App-logo {
	width: 10rem;
}

.flyout {
  display: flex;
  flex-direction: column;
  min-height:100vh;
  justify-content: space-between;
}



#videobackground .gradient {
  background: linear-gradient(to top, #30d0d0b9, #330867b9 100%);
  }

#videobackground .view video {
left: 0!important;
-webkit-transform: none!important;
        transform: none!important;
background-repeat: no-repeat;
background-size: cover;
background-position: center center;
height: 100vh;
}

#videobackground > div.view {
  margin-bottom: 3rem;
}
body {
  margin-top: 66px;
}

